      .row.justify-content-center.align-items-center.mb-4.bg11
        .col-md-10.col-12.p-4
          p.mb-0 El orden operacional y/o de la conexin en cadena de las actividades en la logstica de produccin y servicios se refiere al proceso de comprender, visualizar, e identificar los eventos y/o actividades, que participan en una seguidilla de acciones que se hacen para obtener un resultado por medio de herramientas grficas, listados y otros elementos. #[span.txt-color2 La secuencia operacional es el camino que se traza para ir avanzando y agregando valor a ese proceso requerido para conseguir las metas.] 
        .col-md-2.col-12.px-0
          img(src='@/assets/curso/tema1/img34.png')
      .row.mb-2.align-items-center
        .col-lg-6.bg1.p-3.mb-5
          h3.text-white.text-center.mb-0 Pasos para construir una secuencia operacional
    p La secuencia operacional debe manejar controles y registros para poder hacer los seguimientos pertinentes, y que permitan identificar situaciones susceptibles de ser mejoradas e implementar acciones de mejora. Los resultados del control permiten manejar producciones ms organizadas, los consumos de recursos: materias primas, tiempo, entre otros, y ayudan a verificar cantidades producidas.
    p Para disear e implementar una secuencia de operaciones es necesario seguir los siguientes pasos:
    .row.justify-content-center.align-items-center
      .col-lg-10
        PasosA.color-acento-contenido.mb-5(tipo="l")
          div
            .row.align-items-center
              .col-lg-5.mb-4
                p Definir el sistema de produccin que se va a utilizar.
              .col-auto
                figure
                  img(src='@/assets/curso/tema1/img35.svg', style="width: 170px").m-auto
          div
            .row.align-items-center.justify-content-end
              .col-auto
                figure
                  img(src='@/assets/curso/tema1/img36.svg', style="width: 170px").m-auto
              .col-lg-5.mb-4.text-aling
                p Elaborar un listado de actividades u operaciones: las operaciones, la naturaleza de ellas, los tiempos estndar, y la ubicacin de los puestos de trabajo, son una informacin importante para construir secuencias operacionales. 
          div
            .row.align-items-center
              .col-lg-5.mb-4
                p Establecer cuales equipos y cuales materiales se van a emplear.
              .col-auto
                figure
                  img(src='@/assets/curso/tema1/img37.svg', style="width: 170px").m-auto
          div
            .row.align-items-center.justify-content-end
              .col-auto
                figure
                  img(src='@/assets/curso/tema1/img38.svg', style="width: 170px").m-auto
              .col-lg-5.text-aling
                p Calcular las personas de acuerdo al volumen de la demanda y las operaciones; las personas y los horarios definen la capacidad productiva.
          div
            .row.align-items-center
              .col-lg-5.mb-4
                p Asignar el sitio a los elementos a utilizar, o sea, organizar la distribucin en planta: esto requiere consideraciones como el rea de la cual se dispone, los inventarios que se manejan, y las reas con las que se relacionan o se apoyan. Se organiza la planta de acuerdo a la lnea de produccin, igualmente la prestacin del servicio.
              .col-auto
                figure
                  img(src='@/assets/curso/tema1/img39.svg', style="width: 170px").m-auto
          div
            .row.align-items-center.justify-content-end
              .col-auto
                figure
                  img(src='@/assets/curso/tema1/img40.svg', style="width: 170px").m-auto
              .col-lg-5.mb-4.text-aling
                p Determinar cules controles de trazabilidad se van a realizar para hacerle seguimiento a la secuencia operacional.
          div
            .row.align-items-center
              .col-lg-5.mb-4
                p De acuerdo al servicio o sistema de produccin que se utiliza, se debe acordar la secuencia operacional, y ese sistema est definido por el tipo de producto, el tamao de la empresa, la infraestructura tecnolgica y digital incorporada en los equipos y los materiales que se utilizan, entre otros.
              .col-auto
                figure
                  img(src='@/assets/curso/tema1/img41.svg', style="width: 170px").m-auto
    .row.mb-2.align-items-center
      .col-lg-5.bg1.p-3.mb-5
        h3.text-white.text-center.mb-0 Ventajas de la Secuencia operacional
    .bg10             
      .row.align-items-center.mb-5
        .col-lg-8(data-aos="fade-right")
          p.mb-4 Los procesos productivos de confeccin son cada vez ms dinmicos, su organizacin genera cierta complejidad, por lo cual se requiere una comprensin metdica de sus elementos y de cada interaccin que se de en dicho proceso, con el fin de mantener o mejorar su eficiencia, por eso organizar secuencias operacionales trae ciertas ventajas como son:
          ul.lista-ul--color.lista-ul--separador.mb-4
            li 
              i.fas.fa-cog(style="color:#F14141")
              |
              p.mb-0 Ayudar a identificar procesos o actividades susceptibles de mejora.
            li 
              i.fas.fa-cog(style="color:#F14141")
              |
              p.mb-0 Cuantificar en dinero y/o en tiempo los beneficios de la accin de mejora.
            li 
              i.fas.fa-cog(style="color:#F14141")
              |
              p.mb-0 Ayudar a reorganizar puestos de trabajo y distribucin de planta.
            li 
              i.fas.fa-cog(style="color:#F14141")
              |
              p.mb-0 Identificar operaciones que no estaban presupuestadas. 
            li 
              i.fas.fa-cog(style="color:#F14141")
              |
              p.mb-0 Hacer nuevas mediciones de tiempos.
        .col-lg-4
          figure
            img.mb-5(src='@/assets/curso/tema1/img43.png')(data-aos="fade-left")
    p En resumen, elaborar una secuencia operacional permite visualizar e identificar los eventos relevantes o actividades que van a intervenir en un proceso, calculando tiempos, mquinas e informacin, para de esa manera poder mejorar una situacin actual.
    p A continuacin, veremos un ejemplo de una secuencia operacional, donde est el listado de las operaciones en un orden lgico, con las mquinas donde se ejecuta la operacin, y el tiempo estndar que se demora cada una. Adicional se anotan observaciones pertinentes y que apunten a mejorar la productividad, y los nombres de las personas que hacen o harn la operacin.

