    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.4	Capacidad productiva y cargas de trabajo
    p La estrategia en la planeacin de la capacidad productiva y las cargas de trabajo, es determinar qu tan preciso puede ser el equilibrio de los tres principales recursos de un proceso productivo, las personas, las mquinas y las materias primas. 
    .row.justify-content-center(data-aos="fade-left").mb-5
      .col-lg-6.mb-4
        .tarjeta-avatar-b.mb-0.h-100
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/tema1/img44.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              p.mt-5 La organizacin de los tiempos en cuanto a las distintas actividades del proceso de produccin es crucial para cumplir los objetivos propuestos.
      .col-lg-6.mb-4
        .tarjeta-avatar-b.mb-0.h-100
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/tema1/img45.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              p Por lo tanto, hacer un adecuado balanceo de lneas de produccin, es la mejor tcnica empleada en la asignacin de cargas de trabajo y disear y aplicar la matriz de polifuncionalidad, lo que direcciona a los administrativos al descubrimiento de fortalezas y debilidades de los equipos de colaboradores. 
    p.mb-5 En la situacin actual de pequeos, medianos y grandes empresarios, es necesarios desarrollar habilidades bsicas dirigidas primordialmente a aportar al recurso humano poco calificado, en cmo mejorar los procesos logsticos de produccin y servicios, siempre proyectando unos resultados finales ptimos en cuanto a costos y que satisfagan las necesidades de un cliente en trminos de calidad. 
    .row.align-items-center
      .col-lg-3.bg1.p-3.mb-5
        h3.text-white.text-center.mb-0 Capacidad productiva
    p.mb-5 Las organizaciones pequeas o grandes deben planificar en sus procesos para que se conviertan en entes con altos parmetros en productividad y servicio.
    .row.mb-4.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-6.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img46.png').m-auto
      .col-lg-6.mb-5
        p.mb-5 #[b Se deben crear necesidades como]:
        ul.lista-ul
            li 
              i.fas.fa-cog(style="color:#F14141")
              | El clculo de la capacidad productiva. 
            li 
              i.fas.fa-cog(style="color:#F14141")
              | Estimar las cargas de trabajo y lo que son capaces de producir. 
            li 
              i.fas.fa-cog(style="color:#F14141")
              | Realizar trazabilidad en tiempo presente a sus procesos.
            li 
              i.fas.fa-cog(style="color:#F14141").mb-4
              | Concentrarse en elaborar unos completos listados operacionales.
        p.mb-4.p-5.bg15.shadow  Que permitan una ilustracin directa a los entes gerenciales, del manejo de los costos y un mximo aprovechamiento de los recursos.
        p Planificar ordenada y permanentemente la capacidad productiva y las cargas de trabajo sin importar el tamao de los lotes o el servicio a prestar, ayuda al empresario a desarrollar habilidades para establecer la realidad de su negocio, y se convierte en un instrumento tcnico empleado para responder con puntualidad a los clientes sobre las fechas de entrega de sus productos o servicios.
    .row.mb-2.align-items-center
      .col-lg-5.bg1.p-3.mb-5
        h3.text-white.text-center.mb-0 Capacidad instalada operativa. Horas hombre
    .row.mb-4.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-7
        p Se refiere al nmero de minutos que utiliza el personal de mano de obra directa durante una jornada o periodo de trabajo, al realizar una actividad, utilizar cien por ciento la maquinaria disponible durante el ciclo laboral y calcular matemticamente los minutos empleados para un servicio o una orden de produccin especfica; todo esto, con base en la aplicacin estricta de los parmetros de calidad que se requieren para ensamblar una unidad de produccin o precisamente la prestacin del servicio.
        p #[b Horas hombre]
        p Para las corporaciones de manufactura y servicios hablar de capacidad instalada se refiere a volver tiempo, el componente vital del proceso productivo que es el factor humano. 
        p.p-3.bg23 #[b Factor humano] <br> Tcnicamente llamado minutos u horas hombre, que en operatividad de la infraestructura en maquinaria, tecnologa digital y materias primas agrega valor y provee servicios o productos de ptima calidad. 
      .col-lg-5.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img47.png').m-auto
    .row.mb-4.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-5.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img48.png').m-auto
      .col-lg-7
        p Los resultados obtenidos en clculo de tiempo de las horas hombre se convierten en los elementos para determinar la trazabilidad porcentual de la carga instalada en unidad de tiempo y la asignacin precisa de tareas a la infraestructura productiva.
        p De estos factores dependen que los administrativos, proyecten el futuro econmico que tanto les preocupa, con estrategias gerenciales y el importante conocimiento que poseen los colaboradores del proceso logstico. 
        p.p-3.bg23 #[b Altos parmetros de calidad] <br> El resultado final es abastecer al mercado con productos y servicios que tengan altos parmetros de calidad, lo que significa una ventaja competitiva que las empresas sostienen en el tiempo. 
    .row.mb-5.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-7
        .bloque-texto-c.color-secundario.p-4
          i.fas.fa-quote-right
          p.mb-3 Proveer al mercado de productos que colmen las expectativas de un consumidor final aumenta la demanda, la sostenibilidad y el crecimiento econmico, as como el incremento del empleo para una regin golpeada por la actual situacin de salud y bajas econmicas.
          span  #[b (Valverde Chifla, 2014)].
      .col-lg-5.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img49.png').m-auto
    .row.mb-2.align-items-center
      .col-lg-8.bg1.p-3.mb-5
        h3.text-white.text-center.mb-0 Clculo matemtico para la Carga instalada o minutos de produccin.
    p.mb-4 Para realizar su clculo se emplean las siguientes #[span.txt-color2 variables]:
    .row.justify-content-center.align-items-center.mb-5
      .col-sm-4.col-lg-4.mb-4.mb-lg-0
