          h4.text-center Nmero de personas de Mano de Obra Directa.
          p.pb-3 Se define como el recurso humano directo que interviene en el proceso productivo, es decir, las personas que transforman la materia prima.
      .col-sm-4.col-lg-4.mb-4.mb-lg-0
        .tarjeta--boton1.bg8.text-white.p-4
          .row.justify-content-center
            .col-12
              figure
                img(src='@/assets/curso/tema1/img51.png', alt='Texto que describa la imagen')
          h4.text-center.mb-3 Jornada de trabajo.
          br
          p.pb-3 Periodo de tiempo estimado en la normatividad del Cdigo Sustantivo del Trabajo colombiano, en el que se determinan un nmero de minutos horarios o diarios.
      .col-sm-4.col-lg-4.mb-4.mb-lg-0
        .tarjeta--boton1.bg8.text-white.p-3
          .row.justify-content-center
            .col-12
              figure
                img.img(src='@/assets/curso/tema1/img52.png', alt='Texto que describa la imagen')
          h4.text-center Constante de das productivos por mes.
          p Para obtener este dato se debe calcular un promedio estadstico de la sumatoria de los das hbiles de produccin por cada mes, lo cual numricamente se estima que se trabaja con 24 das.    
    p.mb-5 As, para determinar la  #[span.txt-color2 Capacidad Instalada Operativa] se debe realizar la siguiente  #[span.txt-color2 frmula]:

