      .col-md-9.col-12
        p En la interpretacin tcnica es posible evidenciar que el Mdulo 1 con 9 personas, aporta 540 minutos en una hora, 4.320 minutos en un da y 103.680 minutos en un mes. Igualmente se muestran los resultados en el Mdulo 2 y el rea de Empaque. De esta forma, se presentan mltiples usos que poseen estos datos numricos para la planeacin de la produccin en la empresa. 
