                p.mt-3 Una  infraestructura digital o manual, asigna los valores monetarios para que los lderes de procesos den como resultado final y preciso el precio de venta.
    p.mb-5 Empleamos este sistema cuando se fabrican pedidos especiales cerrados, cumpliendo con unas especificaciones precisas y personalizadas de un cliente. La materia prima, los insumos y ciertos conocimientos aplicados son especiales, por tanto los costos son separados de otras rdenes as sean similares, entre otros ejemplos, joyera, muebles, fiestas, eventos, vestuario a medida, una vivienda. Este sistema es uno de los ms empleados por su eficacia y precisin.        
    .row.mb-5.align-items-center
      .col-lg-4.bg1.p-3
        h3.text-white.text-center.mb-0 Sistemas de inventarios 
    p Un inventario representa la existencia de bienes muebles e inmuebles que tiene la empresa para comerciar con ellos, comparndolos y vendindolos tal cual o procesndolos primero antes de venderlos, en un perodo econmico determinado. El sistema de inventarios permite registrar la cantidad de mercanca existente en una empresa y as mismo determinar el costo de los productos vendidos. Existen varios tipos de sistemas de inventarios, entre ellos se encuentran:
    .tarjeta.tarjeta--azul1.p-4.mb-5
      SlyderA(tipo="b")(data-aos="fade-right")
        .row.align-items-center.justify-content-center
          .col-lg-5.mb-4.mb-lg-0.ps-5
            p #[b El sistema de inventario perpetuo o permanente:]
            p En el sistema de inventario perpetuo, el negocio mantiene un registro continuo para cada artculo del inventario. Estos registros muestran el inventario disponible todo el tiempo y son tiles para preparar los estados financieros mensuales, trimestral o provisionalmente. Este sistema le permite al negocio determinar el costo del inventario final y el costo de las mercancas vendidas directamente de las cuentas contables, sin tener que hacer conteo fsico del inventario.
          .col-lg-7.ps-4
            figure
              img(src='@/assets/curso/tema1/img77.png')
        .row.align-items-center
          .col-lg-5.mb-4.mb-lg-0.ps-5
            p #[b El sistema de inventario peridico:]
            p En el sistema de inventario peridico o conocido tambin como sistema fsico, la empresa no mantiene un registro continuo del inventario disponible; ms bien al final del periodo, la empresa hace un conteo fsico del inventario disponible y aplica los costos unitarios para determinar el costo del inventario final. Esta cifra del conteo fsico del inventario es la que aparece en el Balance General. Se utiliza tambin para calcular el costo de las mercancas vendidas. 
          .col-lg-7.ps-4
            figure
              img(src='@/assets/curso/tema1/img78.png')
        .row.align-items-center
          .col-lg-5.mb-4.mb-lg-0.ps-5
            p-3 El sistema peridico es generalmente utilizado para contabilizar los artculos del inventario que tienen un costo unitario bajo. Los artculos de bajo costo pueden no ser lo suficientemente valiosos para garantizar el costo de llevar un registro al da del inventario disponible. 
            p Para usar este sistema con efectividad, el propietario debe tener la capacidad de controlar el inventario mediante la inspeccin visual. Por ejemplo, cuando un cliente le solicita ciertas cantidades disponibles, el dueo o administrador debe ser capaz de visualizar las mercancas existentes.
          .col-lg-7.ps-4
            figure
              img(src='@/assets/curso/tema1/img79.png')
    .row.mb-5.justify-content-center
      .col-10.bg3.p-4
        .row.align-items-center.justify-content-center
          .col-lg-11
            p.mb-0 Para ampliar estos conceptos sobre los inventarios en una empresa, consultar en el material complementario el sitio web de Gestiopolis la publicacin de #[span.txt-color2 Rebeca Martinez (2001) sobre El inventario].
          .col-lg-1.mb-3
