      h2 1.6 Control de inventario en proceso
    .row.mb-5.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-7
        p Los inventarios entonces necesitan ser clasificados debido a su importancia ya que para el administrador la informacin que suministran tiene que ver con su manejo y esto requiere uso de recursos, personal y dinero. La limitante entonces est en que los recursos son limitados y muchas veces se tiene gran nmero de inventarios.
        p.p-3.bg5 Un inventario representa la existencia de bienes muebles e inmuebles que tiene la empresa para comerciar con ellos, comprndolos y vendindolos tal cual o procesndolos primero antes de venderlos, en un perodo econmico determinado y deben aparecer en el grupo de Activo Circulante.
        p Los inventarios son importantes para los fabricantes en general, y varan ampliamente entre los distintos grupos de industrias. La composicin de esta parte del activo es una gran variedad de artculos, y es por eso que se han clasificado de acuerdo a su utilizacin en los siguientes tipos: 
      .col-lg-5.mb-4.mb-lg-0
        img(src='@/assets/curso/tema1/img81.svg').m-auto
    .row.align-items-center.mb-5
      .col-lg-12
        AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row(titulo="Inventario de materias primas")
            .col-12
              .row.align-items-center
                .col-md-10.align-items-center
                  p.ps-5 Lo conforman todos los materiales con los que se elaboran los productos, pero que todava no han recibido procesamiento. La materia prima, es aquel o aquellos artculos sometidos a un proceso de fabricacin que al final se convertir en un producto terminado.
                  p.ps-5 En toda actividad industrial concurren una variedad de artculos (materias primas) y materiales, los que sern sometidos a un proceso para obtener al final un artculo terminado o acabado. A los materiales que intervienen en mayor grado en la produccin se les considera "materia prima", ya que son utilizadas en cantidades lo suficientemente importantes para lograr el producto acabado.
                .col-lg-2.col-md-6.col-12.p-0
                  figure
                    img.img-t(src='@/assets/curso/tema1/img82.svg', alt='Texto que describa la imagen').mx-auto
          .row(titulo="Inventario de productos en proceso")
            .col-12
                .row.align-items-center
                  .col-md-10.align-items-center
                    p.ps-5 Lo integran todos aquellos bienes adquiridos por las empresas manufactureras o industriales, los cuales se encuentran en proceso de manufactura. Su cuantificacin se hace por la cantidad de materiales, mano de obra y gastos de fabricacin, aplicables a la fecha de cierre.
                    p.ps-5 El inventario de productos en proceso consiste en todos los artculos o elementos que se utilizan en el actual proceso de produccin. Es decir, son productos parcialmente terminados que se encuentran en un grado intermedio de produccin y a los cuales se le aplic la labor directa y gastos indirectos inherentes al proceso de produccin en un momento determinado. Una de las caractersticas del Inventario de produccin en proceso es que va aumentando el valor a medida que es transformado de materia prima en el producto terminado como consecuencia del proceso de produccin. 
                  .col-lg-2.col-md-6.col-12.p-0
                    figure
                      img.img-t(src='@/assets/curso/tema1/img83.svg').mx-auto
          .row(titulo="Inventario de productos terminados")
            .col-12
              .row.align-items-center
                .col-md-10.align-items-center
                  p.ps-5 Son todos aquellos bienes adquiridos por las empresas manufactureras o industriales, los cuales son transformados para ser vendidos como productos elaborados, comprenden estos, los artculos transferidos por el departamento de produccin al almacn de productos terminados por haber alcanzado su grado de terminacin total y que a la hora de la toma fsica de inventario se encuentren an en los almacenes, es decir, los que todava no han sido vendidos. El nivel de inventario de productos terminados va a depender directamente de las ventas, su nivel est dado por la demanda. 
                  p.ps-5 
                .col-lg-2.col-md-6.col-12.p-0
                  figure
                    img.img-t(src='@/assets/curso/tema1/img84.svg').mx-auto
          .row(titulo="Inventario de materiales y suministros")
            .col-12
              .row.align-items-center
                .col-md-10.align-items-center
                  p.ps-5 Son los materiales con los que se elaboran los productos, pero que no pueden ser cuantificados de una manera exacta en el inventario de materiales y suministros se incluye: 
                  p.ps-5 -	Materias primas secundarias, (Pintura, lija, clavos, lubricantes, etc.). Sus especificaciones varan segn el tipo de industria, un ejemplo para la industria cervecera es sales para tratamiento de agua. 
                  p.ps-5 -	Artculos de consumo destinados para ser usados en la operacin de la industria; dentro de estos artculos de consumo los ms importantes son los destinados a las operaciones y estn formados por los combustibles y lubricantes, estos en la industria tienen gran significacin. 
                  p.ps-5 -	Los artculos y materiales de reparacin y mantenimiento de las maquinarias y aparatos operativos. Estos   artculos de reparacin por su gran volumen, necesitan ser controlados adecuadamente y la existencia de estos vara en relacin a sus necesidades. 
                .col-lg-2.col-md-6.col-12.p-0
                  figure
                    img.img-t(src='@/assets/curso/tema1/img85.svg').mx-auto
          .row(titulo="Inventario de mercancas")
            .col-12
              .row.align-items-center
                .col-md-10.align-items-center
                  p.ps-5 Constituye la existencia, a precio de costo, de los artculos comprados o producidos por la empresa, para su comercializacin. Se incluyen en el inventario todas las mercancas de propiedad de la empresa que se encuentran en el almacn, bodegas, en trnsito (cuando la compaa ha comprado fuera del pas) o entregadas en consignacin, se excluyen las que no son propiedad de la empresa pero que se han recibido en consignacin o estn vendidas y an no se han llevado al cliente.
                  p.ps-5 En las empresas comerciales, cuyo objeto es la compra - venta, esta cuenta (inventarios) es lo que genera utilidades, por lo que los inventarios deben ser valorados y controlados tcnicamente. 
                .col-lg-2.col-md-6.col-12.p-0
                  figure
